<template>
  <div class="manage">
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose">

      <!-- 用户的表单信息 -->
      <el-form ref="elform" :rules="rules" :model="form" label-width="80px">
        <el-form-item label="姓名" prop="name">
          <el-input v-model="form.name" placeholder="请输入姓名"></el-input>
        </el-form-item>

        <el-form-item label="年龄" prop="age">
          <el-input v-model="form.age" placeholder="请输入年龄"></el-input>
        </el-form-item>

        <el-form-item label="性别" prop="sex">
          <el-select v-model="form.sex" placeholder="请选择">
            <el-option label="男" :value="1"></el-option>
            <el-option label="女" :value="0"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="出生日期"  prop="birth">
          <el-date-picker
            v-model="form.birth"
            type="date"
            placeholder="选择日期"
            value-format="yyyy-MM-DD">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="地址" prop="addr">
          <el-input v-model="form.addr" placeholder="请输入地址"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>

    <div class="manage-header">
      <el-button type="primary" @click="handleAdd">+ 新 增</el-button>
      <!-- form 搜索区 -->
      <el-form :model="userForm" :inline="true">
        <el-form-item>
          <el-input v-model="userForm.name" placeholder="请输入名称"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    
    <div class="common-table">
      <el-table
        :data="tableData"
        stripe
        style="width: 100%"
        height="100%"
        size="small">
        <el-table-column
          prop="name"
          label="姓名">
        </el-table-column>
        <el-table-column
          prop="sex"
          label="性别">
          <template slot-scope="scope">
            <span>{{ scope.row.sex == 1 ? '男' : '女' }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="age"
          label="年龄">
        </el-table-column>
        <el-table-column
          prop="birth"
          label="出生日期">
        </el-table-column>
        <el-table-column
          prop="addr"
          label="地址">
        </el-table-column>user
        <el-table-column
          prop="handle"
          label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)">编 辑</el-button>
            <el-button type="danger" size="mini" @click="handleDelete(scope.row)">删 除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="pager">
        <el-pagination
          layout="prev, pager, next"
          :total="total"
          @current-change="handlePage"
          :page-size="pageData.limit">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getUser, addUser, editUser, delUser } from '../api'

export default {
  name: 'User',
  data() {
    return {
      dialogVisible: false,
      form: {
        name: '',
        age: '',
        sex: '',
        birth: '',
        addr: '',
      },
      rules: {
        name: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
        ],
        age: [
          { required: true, message: '请输入年龄', trigger: 'blur' },
        ],
        sex: [
          { required: true, message: '请选择性别', trigger: 'blur' },
        ],
        birth: [
          { required: true, message: '请选择日期', trigger: 'blur' },
        ],
        addr: [
          { required: true, message: '请填写地址', trigger: 'blur' },
        ],
      },
      tableData: [],
      modelType: 0,   // 0 表示是新增的弹窗，1 表示是编辑的弹窗
      total: 0,   // 总条数
      // 传递的参数
      pageData: {
        page: 1,
        limit: 15,
      },
      userForm: {
        name: '',
      },
    };
  },
  methods: {
    // 提交用户表单
    submit() {
      this.$refs.elform.validate((valid) => {
        if(valid) {
          this.modelType == 0 ? addUser(this.form).then(() => {
              // 重新获取列表的接口
              this.getList()
            }) : editUser(this.form).then(() => {
              this.getList()
            })
          // 关闭弹窗
          this.dialogVisible = false
          // 清空表单数据
          // this.$refs.elform.resetFields()
          this.form = {
            name: '',
            age: '',
            sex: '',
            birth: '',
            addr: '',
          }
        }
      })
    },
    // 弹窗关闭时触发
    handleClose() {
      this.dialogVisible = false
      // 清空表单数据
      // this.$refs.elform.resetFields()
      this.form = {
        name: '',
        age: '',
        sex: '',
        birth: '',
        addr: '',
      }
    },
    // 新增
    handleAdd() {
      this.modelType = 0
      this.dialogVisible = true
    },
    // 修改
    handleEdit(row) {
      this.modelType = 1
      this.dialogVisible = true
      // 对当前行数据进行深拷贝
      this.form = JSON.parse(JSON.stringify(row))
    },
    // 删除
    handleDelete(row) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        delUser({ id: row.id }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.getList()
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    },
    // 获取的列表数据
    getList() {
      // {...this.userForm, ...this.pageData} 对象属性合并
      getUser({ params: {...this.userForm, ...this.pageData} }).then(({data}) => {
        this.tableData = data.list
        this.total = data.count || 0
      })
    },
    handlePage(val) {
      this.pageData.page = val
      this.getList()
    },
    // 列表查询
    onSubmit() {
      this.getList()
    }
  },
  mounted() {
    this.getList()
  }
}
</script>

<style lang="less" scoped>
  .manage {
    height: 95%;
    .manage-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 60px;
      .el-form {
        height: 40px;
      }
    }
    .common-table {
      position: relative;
      height: calc(100% - 40px);
      .el-table {
        &::before{
          height: 0;
        }
      }
      .pager {
        position: absolute;
        bottom: 0;
        right: 50px;
      }
    }
  }
</style>